"use client";

import React, { useState, useEffect, useRef } from "react";

interface Message {
  id: number;
  sender: "user" | "character";
  content: string;
  timestamp: Date;
}

const historicalFigures = [
  { id: "mandela", name: "Nelson Mandela" },
  { id: "einstein", name: "Albert Einstein" },
  { id: "davinci", name: "Leonardo da Vinci" },
  { id: "cleopatra", name: "Cleopatra" },
];

export default function HistoricalCharacterChat() {
  const [selectedFigure, setSelectedFigure] = useState(historicalFigures[0].id);
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState<string>("");
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSend = () => {
    if (!input.trim()) return;

    const userMessage: Message = {
      id: Date.now(),
      sender: "user",
      content: input.trim(),
      timestamp: new Date(),
    };

    setMessages((prev) => [...prev, userMessage]);
    setInput("");

    // Simulate character response with delay
    setTimeout(() => {
      const characterName = historicalFigures.find(
        (fig) => fig.id === selectedFigure
      )?.name;

      const characterMessage: Message = {
        id: Date.now() + 1,
        sender: "character",
        content: `Respuesta simulada de ${characterName} basada en tu mensaje: ${userMessage.content}`,
        timestamp: new Date(),
      };
      setMessages((prev) => [...prev, characterMessage]);
    }, 1500);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLTextAreaElement>) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  return (
    <div className="max-w-3xl p-4 border border-gray-300 rounded-md bg-white dark:bg-gray-900 dark:border-gray-700 flex flex-col h-[450px]">
      <div className="mb-4">
        <label
          htmlFor="figureSelect"
          className="block mb-1 font-semibold text-gray-700 dark:text-gray-300"
        >
          Selecciona un personaje histórico
        </label>
        <select
          id="figureSelect"
          value={selectedFigure}
          onChange={(e) => setSelectedFigure(e.target.value)}
          className="w-full border border-gray-300 rounded-md p-2 dark:bg-gray-800 dark:border-gray-600 dark:text-white"
        >
          {historicalFigures.map((fig) => (
            <option key={fig.id} value={fig.id}>
              {fig.name}
            </option>
          ))}
        </select>
      </div>

      <div className="flex-1 overflow-y-auto mb-4 space-y-3">
        {messages.length === 0 && (
          <p className="text-gray-500 dark:text-gray-400">
            Comienza a chatear con el personaje histórico seleccionado.
          </p>
        )}
        {messages.map((msg) => (
          <div
            key={msg.id}
            className={`flex ${
              msg.sender === "user" ? "justify-end" : "justify-start"
            }`}
          >
            <div
              className={`max-w-[70%] px-4 py-2 rounded-lg whitespace-pre-wrap ${
                msg.sender === "user"
                  ? "bg-blue-600 text-white"
                  : "bg-gray-200 dark:bg-gray-700 text-gray-900 dark:text-gray-100"
              }`}
            >
              {msg.content}
            </div>
          </div>
        ))}
        <div ref={messagesEndRef} />
      </div>

      <textarea
        aria-label="Escribe tu mensaje"
        rows={2}
        value={input}
        onChange={(e) => setInput(e.target.value)}
        onKeyDown={handleKeyDown}
        className="resize-none w-full border border-gray-300 rounded-md p-2 focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-800 dark:border-gray-600 dark:text-white"
        placeholder="Escribe tu mensaje aquí..."
      />
      <button
        onClick={handleSend}
        className="mt-2 bg-blue-600 text-white rounded-md px-4 py-2 hover:bg-blue-700 transition-colors disabled:opacity-50"
        disabled={!input.trim()}
      >
        Enviar
      </button>
    </div>
  );
}
