"use client";

import React, { useState } from "react";

type MessageType = "text" | "audio" | "video" | "document";

export default function LegacyMessageCreator() {
  const [messageType, setMessageType] = useState<MessageType>("text");
  const [content, setContent] = useState<string>("");
  const [unlockDate, setUnlockDate] = useState<string>("");
  const [unlockTime, setUnlockTime] = useState<string>("");

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    // For now, just alert the data
    alert(
      `Mensaje guardado:\nTipo: ${messageType}\nContenido: ${content}\nFecha de desbloqueo: ${unlockDate} ${unlockTime}`
    );
    // Reset form
    setContent("");
    setUnlockDate("");
    setUnlockTime("");
  };

  return (
    <form
      onSubmit={handleSubmit}
      className="max-w-3xl p-4 border border-gray-300 rounded-md bg-white dark:bg-gray-900 dark:border-gray-700"
    >
      <div className="mb-4">
        <label
          htmlFor="messageType"
          className="block mb-1 font-semibold text-gray-700 dark:text-gray-300"
        >
          Tipo de mensaje
        </label>
        <select
          id="messageType"
          value={messageType}
          onChange={(e) => setMessageType(e.target.value as MessageType)}
          className="w-full border border-gray-300 rounded-md p-2 dark:bg-gray-800 dark:border-gray-600 dark:text-white"
        >
          <option value="text">Texto</option>
          <option value="audio">Audio</option>
          <option value="video">Video</option>
          <option value="document">Documento</option>
        </select>
      </div>

      <div className="mb-4">
        <label
          htmlFor="content"
          className="block mb-1 font-semibold text-gray-700 dark:text-gray-300"
        >
          Contenido
        </label>
        {messageType === "text" && (
          <textarea
            id="content"
            rows={4}
            value={content}
            onChange={(e) => setContent(e.target.value)}
            className="w-full border border-gray-300 rounded-md p-2 resize-none dark:bg-gray-800 dark:border-gray-600 dark:text-white"
            placeholder="Escribe tu mensaje aquí..."
            required
          />
        )}
        {(messageType === "audio" ||
          messageType === "video" ||
          messageType === "document") && (
          <input
            type="file"
            accept={
              messageType === "audio"
                ? "audio/*"
                : messageType === "video"
                ? "video/*"
                : ".pdf,.doc,.docx,.txt"
            }
            onChange={(e) => {
              if (e.target.files && e.target.files.length > 0) {
                setContent(e.target.files[0].name);
              }
            }}
            className="w-full"
            required
          />
        )}
      </div>

      <div className="mb-4">
        <label
          htmlFor="unlockDate"
          className="block mb-1 font-semibold text-gray-700 dark:text-gray-300"
        >
          Fecha de desbloqueo
        </label>
        <input
          type="date"
          id="unlockDate"
          value={unlockDate}
          onChange={(e) => setUnlockDate(e.target.value)}
          className="w-full border border-gray-300 rounded-md p-2 dark:bg-gray-800 dark:border-gray-600 dark:text-white"
          required
        />
      </div>

      <div className="mb-4">
        <label
          htmlFor="unlockTime"
          className="block mb-1 font-semibold text-gray-700 dark:text-gray-300"
        >
          Hora de desbloqueo
        </label>
        <input
          type="time"
          id="unlockTime"
          value={unlockTime}
          onChange={(e) => setUnlockTime(e.target.value)}
          className="w-full border border-gray-300 rounded-md p-2 dark:bg-gray-800 dark:border-gray-600 dark:text-white"
          required
        />
      </div>

      <button
        type="submit"
        className="bg-blue-600 text-white rounded-md px-4 py-2 hover:bg-blue-700 transition-colors"
      >
        Guardar mensaje
      </button>
    </form>
  );
}
