"use client";

import React, { useState } from "react";

interface Recommendation {
  id: number;
  title: string;
  description: string;
}

const sampleRecommendations: Recommendation[] = [
  {
    id: 1,
    title: "Ingeniería de Software",
    description:
      "Basado en tus habilidades y gustos, la ingeniería de software es una excelente opción para ti.",
  },
  {
    id: 2,
    title: "Diseño UX/UI",
    description:
      "Tu perfil sugiere que podrías destacar en diseño de experiencia de usuario y interfaces.",
  },
  {
    id: 3,
    title: "Ciencias de Datos",
    description:
      "Considera una carrera en ciencias de datos para aprovechar tus habilidades analíticas.",
  },
];

export default function CareerRecommendations() {
  const [recommendations, setRecommendations] =
    useState<Recommendation[]>(sampleRecommendations);

  return (
    <div className="max-w-3xl p-4 border border-gray-300 rounded-md bg-white dark:bg-gray-900 dark:border-gray-700">
      <h3 className="text-xl font-semibold mb-4">Recomendaciones Personalizadas</h3>
      {recommendations.length === 0 ? (
        <p className="text-gray-500 dark:text-gray-400">
          No hay recomendaciones disponibles en este momento.
        </p>
      ) : (
        <ul className="space-y-4">
          {recommendations.map((rec) => (
            <li
              key={rec.id}
              className="p-4 border border-gray-200 rounded-md dark:border-gray-700"
            >
              <h4 className="font-semibold text-lg">{rec.title}</h4>
              <p className="mt-1 text-gray-700 dark:text-gray-300">
                {rec.description}
              </p>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
